.eyebrow {
  background-color: $brand;
  // display: none;
  @include desktop {
    display: block;
  }
}

.eyebrow * {
  font-size: 0.8rem;
  color: #fff;
}

.eyebrow ul {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.eyebrow li {
  font-weight: bold;
  &:first-child {
    flex-grow: 2;
  }
}

.eyebrow a {
  display: inline-block;
  padding: 0.75rem 0.5rem;
  text-decoration: none;
}


/*
=========================
FIXED HEADER STYLES
=========================
*/

.header-menu-fixed .eyebrow {
  position: relative;
  top: -2rem
}